#tsparticles {
  position: fixed;
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  z-index: -1;
}

.intro {
  line-height: 1.5;
  height: 100%;
  color: white;

  h1 {
    font-weight: 700;
    font-size: 24px;
  }

  .banner {
    height: 100vh;
  }

  .start {
    position: absolute;
    height: 40px;
    bottom: 40px;
    .btn-start {
      color: white;
      padding: 5px;
    }
  }

  .intro-underlined {
    position: absolute;
    height: 5px;
    width: 100%;
    bottom: 30px;
    background-color: white;
  }
}

